import Notify from '../../components/Notify';

export default {
  middleware: 'guest',
  name: 'login',
  layout: 'primary',
  components: {
    Notify
  },
  data() {
    return {
      form: {
        email: '',
        password: '',
      }
    }
  },
  methods: {

    async login() {
      try {

        await this.$auth.loginWith('local', {data: this.form})

        this.$toasted.success('Logged In, welcome.', {
          theme: "outline", 
          position: "bottom-center", 
          duration : 5000
        });

      } catch (err) {
        console.log(err)
      }
    }

  }
}
